import { defineComponent as _defineComponent } from 'vue'
import { ref } from 'vue'
import useClipboard from 'vue-clipboard3'
import { ImageOutline } from "@vicons/ionicons5";
import { NIcon } from "naive-ui";
import {
  PhotographIcon,
  UploadIcon,
  ClipboardCopyIcon,
  DownloadIcon,
} from '@heroicons/vue/solid'



export default _defineComponent({
  expose: [],
  setup(__props) {

const imageSrc = ref<string>('')
const base64String = ref<string>('')
const { toClipboard } = useClipboard()

function base64ToBlob({
  b64data = '',
  contentType = '',
  sliceSize = 512,
} = {}) {
  return new Promise((resolve) => {
    //  atob() 
    let byteCharacters = atob(b64data)
    let byteArrays = []
    for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
      let slice = byteCharacters.slice(offset, offset + sliceSize)
      let byteNumbers = []
      for (let i = 0; i < slice.length; i++) {
        byteNumbers.push(slice.charCodeAt(i))
      }
      // 8  0
      // 
      byteArrays.push(new Uint8Array(byteNumbers))
    }
    let result = new Blob(byteArrays, {
      type: contentType,
    })
    result = Object.assign(result, {
      // jartto:  URL.createObjectURL
      preview: URL.createObjectURL(result),
      name: `.png`,
    })
    resolve(result)
  })
}

function isBase64(str: string): boolean {
  return str.startsWith('data:image')
}

function saveBase64AsFile(base64: string): void {
  let base64String = base64.split(',')[1]
  base64ToBlob({ b64data: base64String, contentType: 'image/png' }).then(
    (res) => {
      var a = document.createElement('a')
      a.download = new Date().getTime().toString()
      a.href = URL.createObjectURL(res)
      a.click()
    }
  )
}

async function imageToBase64(file: File): Promise<string> {
  return new Promise((resolve, reject) => {
    const reader: FileReader = new FileReader()
    reader.addEventListener('load', () => resolve(reader.result as string))
    reader.addEventListener('error', () => reject(reader.error))
    reader.readAsDataURL(file)
  })
}

function fileSelected(e: Event) {
  const target = e.target as HTMLInputElement
  const file: File = (target.files as FileList)[0]

  imageToBase64(file).then((res) => {
    imageSrc.value = res
    base64String.value = res
  })
}

function handleBase64AreaBlur() {
  if (imageSrc.value === base64String.value) {
    return
  }
  if (base64String.value === '') {
    return
  }
  if (!isBase64(base64String.value)) {
    return
  }
  imageSrc.value = base64String.value
}

async function copyCode() {
  try {
    await toClipboard(base64String.value)
    console.log('Copied to clipboard')
  } catch (e) {
    console.error(e)
  }
}

function downloadImage() {
  saveBase64AsFile(imageSrc.value)
}

return { imageSrc, base64String, toClipboard, base64ToBlob, isBase64, saveBase64AsFile, imageToBase64, fileSelected, handleBase64AreaBlur, copyCode, downloadImage, ref, useClipboard, ImageOutline, NIcon, PhotographIcon, UploadIcon, ClipboardCopyIcon, DownloadIcon }
}

})